  margin-top: -18px;
  margin-left: -18px;
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 0 10px rgba(255, 255, 255, .1), inset 0 0 10px rgba(0, 0, 0, .1), inset 0 2px #090909, inset 0 -1px #888;
